import React from 'react';
import { BrowserRouter } from 'react-router-dom';
import Routes from '../../Routes';
import Menu from '../Menu';

import { Container, Content } from './styles';

const Main: React.FC = () => (
  <BrowserRouter>
    <Container>
      <Menu />
      <Content>
        <Routes />
      </Content>
    </Container>
  </BrowserRouter>
);

export default Main;
